import React, { Component } from 'react';
import './App.css';
import { Button } from 'react-bootstrap';
import autoBind from 'react-autobind';
import 'tui-image-editor/dist/tui-image-editor.css';
import ImageEditor from '@toast-ui/react-image-editor';

const displayMediaOptions = {
  video: {
    cursor: "never"
  },
  audio: false
};


class App extends Component {
	editorRef = React.createRef();
	video = React.createRef();

	state = {
		mode: 'start',
	}

	capture(){
		navigator.mediaDevices.getDisplayMedia(displayMediaOptions).then(stream =>{
			this.video.current.srcObject = stream;
			stream.oninactive = () => this.setState({mode:'start'});
			this.setState({
				mode: 'capture'
			});
		});
	}

	stop(){
		let tracks = this.video.current.srcObject.getTracks();
		tracks.forEach(track => track.stop());
		this.video.current.srcObject = null;

	}
	take(){
		let canvas = document.createElement('canvas');
		canvas.width = this.video.current.videoWidth;
		canvas.height = this.video.current.videoHeight;
		let ctx = canvas.getContext('2d');
		ctx.drawImage(this.video.current, 0, 0, canvas.width, canvas.height);
		let img = canvas.toDataURL();
		const editorInstance = this.editorRef.current.getInstance();
		editorInstance.loadImageFromURL(img, 'Screenshot');
		this.setState({
			mode: 'editor'
		});
	}

	constructor(props) {
		super(props);		
		autoBind(this);
	}

  render() {
		const dMode = m => m === this.state.mode ? '' : 'none';
    return (
			<React.Fragment>
			<div className="parent" >
				<div style={{display: dMode('start')}}>
					<p>Take Screenshots online without installing anything and there's a built-in editor.</p>
					<Button variant="primary" onClick={this.capture}>Start Screen Capture</Button>
				</div>
				<div style={{display: dMode('capture')}}> 
					<Button variant="primary" onClick={this.capture}>New Screen Capture</Button>&nbsp;
					<Button variant="primary" onClick={this.stop}>Stop Capture</Button>&nbsp;

					<Button variant="primary" onClick={this.take}>Take Screenshot</Button>
					<video ref={this.video} autoPlay></video>

				</div>


			</div>
			<div className='editor' style={{display: dMode('editor')}}>
					<ImageEditor
					 includeUI={{
						initMenu: 'crop',
						loadImage: {
							path: 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7',
							name: 'Blank'
						},
						uiSize: {
							width: '100%',
							height: '100%',
						},
						menuBarPosition: 'bottom',
					}}
					selectionStyle={{
						cornerSize: 20,
						rotatingPointOffset: 70,
					}} usageStatistics={false} ref={this.editorRef}/>
				</div>
			</React.Fragment>
		);
  }


}

export default App;
